import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {AuthService} from "../../../services/auth.service";
import {ValidateService} from "../../../services/validate.service";

@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.css']
})
export class ProjectComponent implements OnInit {

  projectname:String;
  description:String;
  projectleader:String;
  category:String;



  constructor(

    private router:Router,
    private validateService: ValidateService,
    private authService: AuthService,
  ) { }

  ngOnInit() {
  }

  registerProjectData() {

    const project = {

      projectname: this.projectname,
      description: this.description,
      projectleader: this.projectleader,
      category: this.category

    };

    if (!this.validateService.validateProjectDetails(project)) {

      //  this.flashMessage.show('All fields required!', { cssClass: 'alert-danger', timeout: 1000 });

      console.log("All fields required!");
      return false;
    }


    this.authService.registerProject(project).subscribe(data =>{


      console.log(data);
      // console.log("data");
      if(data){

        console.log("Your project successfully created!");
        this.router.navigate(['/dashboard']);
      }else{

        console.log("Something went wrong!");
        this.router.navigate(['/dashboard']);

      }



    });

}
}
